/* 1. Use a more-intuitive box-sizing model */
*,
*::before,
*::after {
    box-sizing: border-box;
}

/* 2. Remove default margin */
* {
    margin: 0;
}

body {
    /* 3. Add accessible line-height */
    line-height: 1.5;
    /* 4. Improve text rendering */
    -webkit-font-smoothing: antialiased;
}

/* 5. Improve media defaults */
img,
picture,
video,
canvas,
svg {
    display: block;
    max-width: 100%;
}

/* 6. Inherit fonts for form controls */
input,
button,
textarea,
select {
    font: inherit;
}

/* 7. Avoid text overflows */
p,
h1,
h2,
h3,
h4,
h5,
h6 {
    overflow-wrap: break-word;
}

/* 8. Improve line wrapping */
p {
    text-wrap: pretty;
}
h1,
h2,
h3,
h4,
h5,
h6 {
    text-wrap: balance;
}

/* 9. Create a root stacking context */
#root,
#__next {
    isolation: isolate;
}

:root {
    --color-main: rgb(21, 97, 238);
    --color-highlight: rgb(38, 201, 230);
    --color-error: rgb(223, 30, 127);
    --color-error-dark: rgb(121, 0, 47);
    --color-white: rgb(255, 255, 255);
    --color-black: rgb(0, 0, 0);
    --color-gray-light: rgb(224, 224, 224);
    --color-gray-medium: rgb(192, 192, 192);
    --color-gray-dark: rgb(128, 128, 128);
    --color-shadow: rgba(20, 20, 40, 0.1);

    --shadow: var(--color-shadow) 0px 4px 8px;

    --padding-tiny: 4px;
    --padding-small: 8px;
    --padding-base: 16px;
    --padding-large: 24px;
    --padding-huge: 48px;
    --padding-2x-huge: 96px;

    --border-radius: 8px;
    --border-radius-small: 4px;
}

html {
    scrollbar-gutter: stable;
}

body {
    font-family: sans-serif;
}

button {
    background-color: transparent;
    border: none;
    margin: 0;
    padding: 0;
    text-align: inherit;
    border-radius: 0;
    appearance: none;
}

button:hover, .space-interactive:hover, .space {
    cursor: pointer;
}

.boards-outer-wrapper {
    display: flex;
    gap: var(--padding-2x-huge);
    justify-content: center;
    padding: var(--padding-huge);
}

.board-wrapper {
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: var(--padding-huge);
}

.board {
    display: grid;
    position: relative;
}

.space {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 56px;
    height: 56px;
    border: 4px solid var(--color-white);
    transition: background-color .1s;
}

.space-ship {
    background-color: var(--color-main);
}

.space-empty {
    background-color: rgb(196, 196, 196);
}

.space-hidden {
    background-color: rgb(45, 43, 50);
}

.space-hover {
    background-color: rgb(30, 163, 81);
}

#info-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: var(--padding-base);
}

.info {
    font-size: 1.75rem;
}

.start-btn {
    font-size: 1.75rem;
    background-color: var(--color-main);
    padding: var(--padding-small) var(--padding-large);
    border-radius: var(--border-radius);
}

.start-btn:hover {
    background-color: var(--color-highlight);
}

.board-button-icon {
    width: 32px;
    height: 32px;
}

.board-button {
    display: none;
}

.space-hover .board-button {
    display: initial;
}

.ship-dummy {
    background-color: var(--color-highlight);
}

.space-ship.ship-dummy-illegal {
    background-color: var(--color-error-dark);
}

.ship-dummy-illegal, .blink {
    background-color: var(--color-error);
}